<template>
  <div class="list">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">{{'名称：' + deviceName}}</div>
          <span class="empty"></span>
           <Button type="default" @click="back" style="width:80px">
             <i :class="['iconfont', 'icon-return']"></i>
             返回
             </Button>
        </div>
      </div>
          <div class="btnGroup">
      <div :style="{backgroundColor: tab === 1 ?'#0084FF':'#000',color:(tab === 1 ?'#fff':'rgba(255,255,255,0.6)')}" @click="changeTab(1)">故障树诊断</div>
    </div>
      <div class="common-flex">
        <div class="split-box">
            <img class="healthTree" :src="healthTree">
        </div>
        <div class="split-box" style="padding:21px">
          <div class="header-sub" >
            <div class="title">
              <div class="breadcrumb-item-link">故障判断信息</div>
              <span class="empty"></span>
            </div>
          </div>
          <Table class="m-table no-scroll no-border"
                stripe
                :loading="pageInfo.loading"
                :columns="tableColumns"
                :row-class-name="rowClassName"
                @on-row-click="onRowClick"
                :data="tableData"></Table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import { getHealthTreeSingle, getHealthTreeSingleInfo } from '@/api/device'
export default {
  components: {
    StatusSelect,
    LaboratoryList
  },
  data () {
    return {
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [],
      maxlength: 50,
      selectLocation: '',
      tableColumns: [
        {
          title: '序号',
          key: 'id',
          align: 'left',
          width: 100
        },
        {
          title: '事件名称',
          key: 'eventName',
          align: 'left',
          width: 100
        },
        {
          title: '事件描述',
          key: 'comment',
          align: 'left',
          width: 200
        },
        {
          title: '测点及事件判断逻辑',
          key: 'detailInfo',
          align: 'left',
          width: 250,
          render: (h, params) => {
            let info = JSON.parse(params.row.detailInfo)
            return h('div', info.item_name + info.symbol + info.decision)
          }
        },
        {
          title: '结果',
          key: 'isFault',
          align: 'left',
          render: (h, params) => {
            let info = params.row.isFault
            return h('div', info === 'uncertain' ? '未探明事件' : (info ? '判定故障' : '非故障'))
          }
        }
      ],
      currentIndex: 0,
      title: '',
      type: '',
      healthTree: '',
      tab: 1
    }
  },
  computed: {
    deviceName () {
      return this.$route.query.deviceName
    }
  },
  methods: {
    changeTab (e) {
      this.tab = e
    },
    back () {
      this.$router.push({
        name: 'health-check-list'
      })
    },
    getlocation (e) {
      this.formValue.longitude = e.lng
      this.formValue.latitude = e.lat
    },
    loadTableData () {
      let {treeId, dataId} = this.$route.query
      this.pageInfo.loading = true
      getHealthTreeSingleInfo({treeId, dataId}).then(res => {
        let {code, data} = res
        this.pageInfo.loading = false
        if (code === 200) {
          this.tableData = data
        } else {
          this.$Message.warning(res.msg)
        }
      })
      getHealthTreeSingle({treeId}).then(res => {
        if (res.code === 200) {
          this.healthTree = res.data.accidentTreebase64
        }
      })
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    }
  },
  mounted () {
    this.loadTableData()
  }
}
</script>

<style lang="less" scoped>
@import '../../../../style/varient.less';
@size-padding: 10px;
.title{
  margin-bottom:24px;
  font-size: 14px;
  font-family: Alibaba PuHuiTi;
  font-weight: 400;
  line-height: 32px;
  color: #FFFFFF;
}

.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
    .common-flex{
      display: flex;
      .split-box{
        width: 50%;
        background: #21232E;
        height: 700px;
        margin-right: 2px;
        .healthTree{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
.btnGroup{
  display: flex;
  align-items: center;
  margin: 12px auto 0;

  div{
    margin-right: 6px;
    cursor: pointer;
    padding: 6px 12px;
    border-radius:4px 4px  0px 0px;
    // border: 1px solid #000;
  }
}
</style>



